.course {
  display: inline-block;
  /* width: 225px; */
  color: black;
  position: relative;
  border: 10px solid transparent;
}

.course:hover .course-img {
  filter: brightness(70%);
}

.course-img {
  border: 1px solid #cccccc;
  width: 100%;
  /* object-fit: cover; */
}

.course-name {
  margin-bottom: 0px;
  margin-top: 5px;
  font-size: 16px;
  font-weight: bold;
}

.course-author {
  margin-top: 3px;
  margin-bottom: 0px;
  color: rgb(94, 94, 94);
  font-size: smaller;
}

.course-price {
  display: inline;
  font-weight: bold;
  font-size: 18px;
}

.course-old-price {
  margin-left: 10px;
  margin-top: 0px;
  display: inline-block;
  color: rgb(94, 94, 94);
  text-decoration: line-through;
}

.bestseller {
  background-color: #eceb98;
  color: #3d3c0a;
  width: 55px;
  padding: 4px 8px 4px 8px;
  margin-top: -8px;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
}

.popover-container {
  max-width: 340px;
  padding: 24px;
  box-sizing: border-box;
}

.popover-title {
  margin: 0px;
  font-size: 19px;
}
